.for {
    &__title {
        font-family: Montserrat;
        font-weight: bold;
        font-size: 40px;
        line-height: 60px;
        color: #EFF3FF;
        max-width: 480px;
    }
    &__subtitle {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1.1px;
        color: #FFA161;
    }
    &__descr {
        font-weight: normal;
        font-size: 16px;
        color: #EFF3FF;
    }
    &__wrapper {
        display:flex;
        justify-content: space-between;
        align-items: center;
    }
    &__item {
        width: 317px;
        height: 348px;
        background: rgba(61, 137, 225, 0.16);
        border-radius: 12px;
    }
}